'use client';

import { useEffect, useRef } from 'react';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';
import 'leaflet-routing-machine';
import 'leaflet-routing-machine/dist/leaflet-routing-machine.css';

// Extend the L namespace with routing types
declare global {
  namespace L {
    namespace Routing {
      interface RoutingControl extends L.Control {
        getPlan(): RoutingPlan;
        getRouter(): any;
        route(): void;
      }

      interface RoutingControlOptions {
        waypoints: L.LatLng[];
        routeWhileDragging?: boolean;
        showAlternatives?: boolean;
        addWaypoints?: boolean;
        draggableWaypoints?: boolean;
        fitSelectedRoutes?: boolean;
        lineOptions?: {
          styles: {
            color: string;
            opacity: number;
            weight: number;
          }[];
        };
        createMarker?(i: number, waypoint: any, n: number): L.Marker;
      }

      interface RoutingPlan extends L.Evented {
        setWaypoints(waypoints: L.LatLng[]): RoutingPlan;
      }
    }

    function routing(options: Routing.RoutingControlOptions): Routing.RoutingControl;
  }
}

// Fix for default markers
const DefaultIcon = L.Icon.Default;
L.Icon.Default.imagePath = 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/';

interface MapProps {
  departureCoords: [number, number];
  destinationCoords: [number, number];
  onMapClick?: (coords: [number, number]) => void;
  onMarkerDrag?: (coords: [number, number]) => void;
  interactive?: boolean;
}

const Map = ({ departureCoords, destinationCoords, onMapClick, onMarkerDrag, interactive = true }: MapProps) => {
  const mapRef = useRef<L.Map | null>(null);
  const routingControlRef = useRef<any>(null);

  useEffect(() => {
    // Validate coordinates
    if (!departureCoords?.[0] || !departureCoords?.[1] || !destinationCoords?.[0] || !destinationCoords?.[1]) {
      console.error('Invalid coordinates provided:', { departureCoords, destinationCoords });
      return;
    }

    // Define marker icons
    const blueIcon = new L.Icon({
      iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png',
      shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png',
      iconSize: [25, 41],
      iconAnchor: [12, 41],
      popupAnchor: [1, -34],
      shadowSize: [41, 41]
    });

    const redIcon = new L.Icon({
      iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png',
      shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png',
      iconSize: [25, 41],
      iconAnchor: [12, 41],
      popupAnchor: [1, -34],
      shadowSize: [41, 41]
    });

    // Ensure clean initialization
    if (mapRef.current) {
      if (routingControlRef.current) {
        try {
          routingControlRef.current.remove();
          routingControlRef.current = null;
        } catch (error) {
          console.error('Error removing routing control:', error);
        }
      }
      mapRef.current.remove();
      mapRef.current = null;
    }

    try {
      // Initialize map
      mapRef.current = L.map('map', {
        center: departureCoords,
        zoom: 13,
        layers: [
          L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
            attribution: '© OpenStreetMap contributors'
          })
        ]
      });

      // Create and add routing control with custom markers
      routingControlRef.current = L.Routing.control({
        waypoints: [
          L.latLng(departureCoords[0], departureCoords[1]),
          L.latLng(destinationCoords[0], destinationCoords[1])
        ],
        routeWhileDragging: false,
        showAlternatives: false,
        addWaypoints: false,
        draggableWaypoints: false,
        fitSelectedRoutes: true,
        lineOptions: {
          styles: [
            { color: '#4F46E5', opacity: 0.8, weight: 6 }
          ]
        },
        createMarker: (i: number, waypoint: any) => {
          // Use different icons for departure and destination
          const icon = i === 0 ? blueIcon : redIcon;
          const marker = L.marker(waypoint.latLng, { icon });
          marker.bindPopup(i === 0 ? 'Отправление' : 'Прибытие');
          return marker;
        },
        show: false // Hide the routing instructions panel
      }).addTo(mapRef.current);

      // Fit bounds to show the entire route
      const bounds = L.latLngBounds([departureCoords, destinationCoords]);
      mapRef.current.fitBounds(bounds, { padding: [50, 50] });

      // Add click handler if interactive
      if (interactive && onMapClick) {
        mapRef.current.on('click', (e: L.LeafletMouseEvent) => {
          const { lat, lng } = e.latlng;
          onMapClick([lat, lng]);
        });
      }
    } catch (error) {
      console.error('Error initializing map or routing:', error);
    }

    // Cleanup function
    return () => {
      if (routingControlRef.current) {
        try {
          routingControlRef.current.remove();
          routingControlRef.current = null;
        } catch (error) {
          console.error('Error removing routing control:', error);
        }
      }
      if (mapRef.current) {
        try {
          mapRef.current.remove();
          mapRef.current = null;
        } catch (error) {
          console.error('Error removing map:', error);
        }
      }
    };
  }, [departureCoords, destinationCoords, interactive, onMapClick]);

  return (
    <div id="map" className="w-full h-full rounded-lg shadow-md" style={{ minHeight: '400px' }} />
  );
};

export default Map; 