'use client';

import { useEffect, useState } from 'react';
import { Card, Row, Col, Form, Input, DatePicker, InputNumber, Select, Button, Space, List, Tag, Divider, Spin } from 'antd';
import { SearchOutlined, FilterOutlined, SortAscendingOutlined } from '@ant-design/icons';
import dayjs from 'dayjs';
import Map from '@/components/Map';

// Default coordinates for Moscow center
const DEFAULT_COORDS: [number, number] = [55.7558, 37.6173];

interface Trip {
  id: number;
  driver: {
    username: string;
    driver_rating: number;
  };
  departure_location: string;
  destination_location: string;
  departure_coords: string;
  destination_coords: string;
  date: string;
  time: string;
  price: number;
  available_seats: number;
  status: string;
  description: string;
}

const AllTripsPage = () => {
  const [trips, setTrips] = useState<Trip[]>([]);
  const [loading, setLoading] = useState(false);
  const [filters, setFilters] = useState({
    date_from: '',
    date_to: '',
    price_min: '',
    price_max: '',
    seats: '',
    departure_city: '',
    destination_city: '',
  });
  const [sortField, setSortField] = useState('date');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('asc');

  const fetchTrips = async () => {
    setLoading(true);
    try {
      const queryParams = new URLSearchParams();
      
      // Добавляем фильтры в запрос
      if (filters.date_from) queryParams.append('date_from', filters.date_from);
      if (filters.date_to) queryParams.append('date_to', filters.date_to);
      if (filters.price_min) queryParams.append('price_min', filters.price_min);
      if (filters.price_max) queryParams.append('price_max', filters.price_max);
      if (filters.seats) queryParams.append('seats', filters.seats);
      if (filters.departure_city) queryParams.append('departure_city', filters.departure_city);
      if (filters.destination_city) queryParams.append('destination_city', filters.destination_city);
      
      // Добавляем сортировку
      queryParams.append('ordering', `${sortOrder === 'desc' ? '-' : ''}${sortField}`);

      const response = await fetch(`/api/trips?${queryParams.toString()}`);
      if (!response.ok) throw new Error('Failed to fetch trips');
      const data = await response.json();
      setTrips(data);
    } catch (error) {
      console.error('Error fetching trips:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTrips();
  }, [filters, sortField, sortOrder]);

  const handleFilterChange = (values: any) => {
    setFilters({
      ...filters,
      ...values,
    });
  };

  const handleSortChange = (value: string) => {
    if (value === sortField) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(value);
      setSortOrder('asc');
    }
  };

  const renderTripCard = (trip: Trip) => {
    // Safely parse coordinates with validation
    const parseCoords = (coordsStr: string): [number, number] | null => {
      try {
        const [lat, lng] = coordsStr.split(',').map(Number);
        if (isNaN(lat) || isNaN(lng)) {
          console.error('Invalid coordinates format:', coordsStr);
          return null;
        }
        return [lat, lng];
      } catch (error) {
        console.error('Error parsing coordinates:', error);
        return null;
      }
    };

    const departureCoords = parseCoords(trip.departure_coords) || DEFAULT_COORDS;
    const destinationCoords = parseCoords(trip.destination_coords) || DEFAULT_COORDS;

    return (
      <Card
        key={trip.id}
        className="mb-4 shadow-sm hover:shadow-md transition-shadow"
        title={
          <div className="flex justify-between items-center">
            <span>{trip.departure_location} → {trip.destination_location}</span>
            <Tag color={trip.status === 'active' ? 'green' : 'default'}>
              {trip.status}
            </Tag>
          </div>
        }
      >
        <Row gutter={[16, 16]}>
          <Col span={12}>
            <div className="space-y-2">
              <p><strong>Водитель:</strong> {trip.driver.username}</p>
              <p><strong>Рейтинг водителя:</strong> {trip.driver.driver_rating}</p>
              <p><strong>Дата:</strong> {dayjs(trip.date).format('DD.MM.YYYY')}</p>
              <p><strong>Время:</strong> {trip.time}</p>
              <p><strong>Цена:</strong> {trip.price} ₽</p>
              <p><strong>Свободных мест:</strong> {trip.available_seats}</p>
              {trip.description && (
                <p><strong>Описание:</strong> {trip.description}</p>
              )}
            </div>
          </Col>
          <Col span={12}>
            <div className="h-[200px] rounded-lg overflow-hidden">
              <Map
                departureCoords={departureCoords as [number, number]}
                destinationCoords={destinationCoords as [number, number]}
                interactive={false}
              />
            </div>
          </Col>
        </Row>
      </Card>
    );
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-2xl font-bold mb-6">Все поездки</h1>
      
      <Card className="mb-6">
        <Form layout="vertical" onFinish={handleFilterChange}>
          <Row gutter={[16, 16]}>
            <Col span={8}>
              <Form.Item label="Откуда" name="departure_city">
                <Input placeholder="Введите город отправления" prefix={<SearchOutlined />} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Куда" name="destination_city">
                <Input placeholder="Введите город назначения" prefix={<SearchOutlined />} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Количество мест" name="seats">
                <InputNumber min={1} placeholder="Мин. кол-во мест" style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Дата от" name="date_from">
                <DatePicker style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Дата до" name="date_to">
                <DatePicker style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Цена">
                <Space>
                  <InputNumber
                    placeholder="От"
                    onChange={(value) => handleFilterChange({ price_min: value })}
                  />
                  <InputNumber
                    placeholder="До"
                    onChange={(value) => handleFilterChange({ price_max: value })}
                  />
                </Space>
              </Form.Item>
            </Col>
          </Row>
          <Row justify="end">
            <Space>
              <Button icon={<FilterOutlined />} type="primary" htmlType="submit">
                Применить фильтры
              </Button>
              <Select
                defaultValue="date"
                style={{ width: 200 }}
                onChange={handleSortChange}
                options={[
                  { label: 'По дате', value: 'date' },
                  { label: 'По цене', value: 'price' },
                  { label: 'По количеству мест', value: 'available_seats' },
                ]}
                prefix={<SortAscendingOutlined />}
              />
            </Space>
          </Row>
        </Form>
      </Card>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <Spin size="large" />
        </div>
      ) : (
        <List
          dataSource={Array.isArray(trips) ? trips : []}
          renderItem={renderTripCard}
          pagination={{
            pageSize: 10,
            total: Array.isArray(trips) ? trips.length : 0,
            showSizeChanger: false,
          }}
        />
      )}
    </div>
  );
};

export default AllTripsPage; 